@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&family=Flow+Circular&family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

div {
  font-family: "DM Sans", sans-serif;
}

.maintext {
 /* Responsive font size */
  font-weight: 300;
  line-height: 1.0;
}

.submaintext {
  font-weight: 300;
  font-size: 2vw; /* Responsive font size */
}

.skills-container {
  overflow: hidden;
  display: flex;
  align-items: center;
  position: relative;
}

.move {
  height: 14vh; /* Responsive height */
  width: 14vw; /* Responsive width */
  position: absolute;
  left: calc(100% + (var(--position) * 14vw)); /* Responsive spacing */
  animation: slide 15s linear infinite;
  animation-delay: calc((15s / 11) * (var(--position) - 1));
}

@keyframes slide {
  0% {
    left: 100%;
  }
  100% {
    left: -15vw; /* Responsive negative left position */
  }
}

.grad {
  mask-image: linear-gradient(
    to right,
    transparent,
    #000000,
    transparent
  );
}

.skills-container:hover .move {
  animation-play-state: paused !important;
}

.test {
  transform: translateY(0px);
  opacity: 0.7;
  animation: updown 5s linear infinite;
}

@keyframes updown {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(1vw); /* Responsive translation */
  }
  100% {
    transform: translateY(0px);
  }
}

.test:hover {
  font-size: 4vw; /* Responsive font size */
  opacity: 1;
}

/* Media Queries for specific devices */

@media (max-width: 768px) {
  .maintext {
    font-size: 8vw; /* Adjust font size for tablets and small screens */
  }

  .submaintext {
    font-size: 4vw; /* Adjust font size for tablets and small screens */
  }

  .move {
    height: 15vw;
    width: 15vw;
  }
  
  .test {
    font-size: 6vw;
  }
}

@media (max-width: 480px) {
  .maintext {
    font-size: 10vw; /* Adjust font size for mobile phones */
  }

  .submaintext {
    font-size: 5vw; /* Adjust font size for mobile phones */
  }

  .move {
    height: 20vw;
    width: 20vw;
  }

  .test {
    font-size: 8vw;
  }
}
